<template>
    <div class="pay-table">
        <div class="goods">
            <p class="bold tile">订单信息</p>
            <table>
                <tr class="tile">
                    <td class="ac">商品信息</td>
                    <td class="ac">单价</td>
                    <td class="ac">数量</td>
                    <td class="ac">收货人</td>
                </tr>
                <tr v-for="(i,k) in order" :key="k">
                    <td style="padding:10px 20px">
                        <img
                            style="width: 60px;height: 60px;margin-right: 10px"
                            :src="i.default_img | img" alt="">{{i.product_name}}</td>
                    <td class="ac">{{i.goods_point/exch}}{{unit}}</td>
                    <td class="ac">{{i.nums}}</td>
                    <td class="ac" >{{i.shouhuo_name}}</td>
                </tr>
                <tr>
                    <td colspan="4" class="ar" style="padding-right: 10px">
                        运费：<span style="font-size: 16px;font-weight: bold;color: #ff0077">{{expressFee/exch}}</span>{{unit}}
                        合计：<span style="font-size: 16px;font-weight: bold;color: #ff0077">{{subTotal/exch}}</span>{{unit}}
                    </td>
                </tr>
            </table>
        </div>
        <div class="payment">
            <p class="bold tile">支付方式</p>
            <div class="choose-payment">
                <p style="padding-left: 20px;margin-bottom: 30px;"
                >您当前账户余额：{{point/exch}}{{unit}}<span v-if="subTotal>point">，不足以抵扣。差额<em style="color: #f00070">{{(subTotal-point)/exch}}{{unit}}</em>可使用以下方式支付 。</span></p>
                <ul v-if="!Dikou">
                    <!--<li class="mixed-payment">-->
                        <!--<span>是否启用混合支付</span>-->
                        <!--<input type="checkbox" v-model="Dikou">-->
                    <!--</li>-->
                    <li :class="['pay-list','ac','cursor',{'selected-pay':DefaultPayment===i.type}]"
                        v-for="(i,k) in payment"
                        @click="DefaultPayment = i.type"
                        :key="k"
                        v-if="i.allow"
                    >
                        {{i.title}}
                        <img
                                v-show="DefaultPayment===i.type"
                                src="../assets/img/order/icon_checked_2.png" alt="">
                    </li>
                </ul>
                <ul v-else>
                    <li class="mixed-payment">
                        <span>是否启用混合支付</span>
                        <input type="checkbox" v-model="Dikou">
                        <input type="text" placeholder="需要抵扣的积分数量" v-model="dikoujifen">
                    </li>
                    <li :class="['pay-list','ac','cursor',{'selected-pay':DefaultPayment===i.type}]"
                        v-for="(i,k) in payment"
                        @click="DefaultPayment = i.type"
                        v-if="i.type!=='JifenPay'"
                        :key="k"
                    >
                        {{i.title}}
                        <img
                                v-show="DefaultPayment===i.type"
                                src="../assets/img/order/icon_checked_2.png" alt="">
                    </li>
                </ul>
            </div>
        </div>
        <div class="pay-btn ac cursor" @click="pay">
            立即支付
        </div>
    </div>
</template>

<script>
    let display = {
        showRecommend: false,
        nav: 'hidden',
        header: 'fault'
    };
export default {
    name: 'pay-table',
    metaInfo: {
        title: '支付',
        meta: [
            {name: 'keywords', content: '福分通'},
            {name: 'description', content: '专注企业员工福利'}
        ]
    },
    created(){
        this.$store.dispatch('layout/Change', display);
        this.getMemberInfo();
        this.getOrderInfo();

    },
    mounted(){
      // window.onpopstate = ()=>{
      //     this.$router.push('cart')
      // }
    },
    beforeDestroy(){
        window.onpopstate = null
    },
    data() {
        return {
            dikoujifen:'',
            DefaultPayment:'JifenPay',
            isDikou:false,//JifenPay,AliPay,WeChatPay,isDikou
            payment:[
                {title:'余额支付',type:'JifenPay',allow:true},
                {title:'支付宝',type:'AliPay',allow:true},
                {title:'微信支付',type:'WeChatPay',allow:true}
            ],
            order:[],
            point:this.storage.getStorage('fft-user-info').point,
            exch:'1',
            unit:'',
            total:0,
            expressFee:0
        }
    },
    methods:{
        getMemberInfo(){
          this.yum('api/member/getMemberInfo').then(res=>{
              if (res.data.status === 200){
                  this.point = res.data.data.point;
                  this.exch = parseInt(res.data.exchange);
                  this.unit = res.data.unit
              }
          })
        },
        getOrderInfo(){
            let order_sn = this.$route.query.order_sn || null;
            if (typeof (this.$route.query.order_sn) === "string"){
                order_sn = [this.$route.query.order_sn]
            }
            this.yum('api/order/getOrderDetail',{
                order_sn:order_sn
            }).then(res=>{
                if (res.data.status === 200){
                    let data = [];
                    let total  = 0;
                    let expressFee = 0;
                    res.data.data.forEach(i=>{
                            data = data.concat(i.goods_child);
                            total+=i.subtotal;
                            expressFee+=i.express_fee
                    });
                    this.order = data;
                    this.total = total;
                    this.expressFee = expressFee;
                    if (this.total+ this.expressFee>parseInt(this.point)){
                        this.payment[0].allow = false;
                        this.DefaultPayment = 'AliPay'
                    }
                }else{
                    this.$router.push('/cart')
                }
            })
        },
        pay(){
            let payType = this.DefaultPayment;let orderSn = this.$route.query.order_sn;
            let token = this.storage.getStorage('FFT').token;
            let city = this.storage.getStorage('FFT').business_code;
            // let openId = 'obaIs0yEbaH7JVY0DSne-6pxNBNE';
            let host = location.hostname.split('.')[0];
            if ( host === 'localhost'||host === '192'){
                host = 'mapi'
            }else{
                host = 'mapi'
            }
            if (this.DefaultPayment === 'WeChatPay') {
                this.bus.$emit('loading',`即将跳转到第三方支付`);
                setTimeout(function(){
                    location.href = `http://${host}.fufentong.com/${city}/api/order/pay?payType=${payType}&orderSn[]=${orderSn}&token=${token}&wx=2`
                },1000)
            }else if(this.DefaultPayment === 'AliPay'){
                this.bus.$emit('loading',`即将跳转到第三方支付`);
                setTimeout(function(){
                    location.href = `http://${host}.fufentong.com/${city}/api/order/pay?payType=${payType}&orderSn[]=${orderSn}&token=${token}&ali=1`
                },1000)
            }else{
                this.bus.$emit('loading',`即将跳转到积分支付`);
                setTimeout(function(){
                    location.href = `http://${host}.fufentong.com/${city}/api/order/pay?payType=${payType}&orderSn[]=${orderSn}&token=${token}&jifen=1`
                },1000)
            }

        }
    },
    computed:{
        Dikou:{
            get(){
                return this.isDikou
            },
            set(e){
                this.isDikou = e
                if (e){
                    this.DefaultPayment = 'AliPay'
                }
            }
        },
        subTotal(){
            return this.total+this.expressFee
        }
        // paytype(){
        //     if (this.total>this.point&&this.DefaultPayment === 'JifenPay') {
        //         return false
        //     }else{
        //         return true
        //     }
        // }
    }
}
</script>

<style lang="stylus" scoped>
    .pay-table
        min-width 100%
        min-height 600px
        margin 40px 0 0 0
        position relative
        &.router
            margin-left 0
    .tile
        font-size 16px
        color black
        margin-bottom 30px
    .goods
        position relative
        margin-bottom 20px
        table
            width 100%
            tr.tile
                min-height 40px
                background-color #e4e4e4
                td
                    height 40px
            td
                border 1px solid #ccc
                min-height 40px
            tr
                min-height 40px
                line-height 40px
    .choose-payment
        position relative
        margin-bottom 30px
        ul
            position relative
            margin-bottom 10px
            padding-left 20px
        li
            position relative
            display inline-block
            width 100px
            height 26px
            line-height 22px
            border 2px solid #ccc
            margin-right 10px
            &.selected-pay
                border-color #ff0077
            img
                position absolute
                bottom -2px
                right -2px
            &.mixed-payment
                border none
                width auto
                display block
                height 30px
                line-height 30px
                margin-bottom 20px
            input[type=checkbox]
                width 20px
                height 20px
                margin-left 5px
                vertical-align middle
            input[type=text]
                height 26px
                margin-left 5px
    .pay-btn
        width 120px
        height 40px
        line-height 40px
        font-size 18px
        background-color #ff0077
        color white
        margin  0 0 0 auto
</style>
